import { Container } from "./styles";
import Button from "../button";
import Image from "../../assets/online.jpg";

const Online = () => {
    return(
        <Container>
            <div className="width">
                <div className="left">
                    <h2>Consulta online</h2>
                    <p>A consulta online contempla um encontro via Zoom, além de um bate-papo para compreender objetivos e questões comportamentais/alimentares + rastreamento metabólico + avaliação por fotos/peso/medidas, exames bioquímicos, planejamento alimentar individualizado e suplementação conforme necessidade.A consulta tem duração média de 1h30min a 1h.</p>
                    <Button
                        value="Agende sua consulta online"
                    />
                </div>
                <img src={Image} alt="Pessoa em escritório" />
            </div>
        </Container>
    );
}

export default Online;