import { Container } from "./styles";
import { useState } from "react";
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import ArrowDropUpIcon from '@mui/icons-material/ArrowDropUp';

const Skills = () => {
    const [box1, setBox1] = useState(false);
    const [box2, setBox2] = useState(false);
    const [box3, setBox3] = useState(false);
    const [box4, setBox4] = useState(false);
    const [box5, setBox5] = useState(false);
    const [box6, setBox6] = useState(false);


    return(
        <Container>
            <h2>Minhas especialidades</h2>
            <div className="display">
                <div className="boxes">
                    <div
                        onClick={() => box1 ? setBox1(false) : setBox1(true)}
                        className="box1"
                    >
                        <p>Saúde da mulher</p>
                        {box1 &&
                            <ArrowDropDownIcon />
                        }
                        {!box1 &&
                            <ArrowDropUpIcon />
                        }
                    </div>
                    <div
                        style={{height: box1 ? '170px' : '0' }}
                        className="box2"
                    >
                        <p>
                            Estratégias nutricionais e suplementação para Síndrome pré-menstrual (TPM), síndrome do ovário policístico (SOP), endometriose, libido, candidíase, climatério e menopausa.
                        </p>
                    </div>
                </div>
                <div className="boxes">
                    <div
                        onClick={() => box2 ? setBox2(false) : setBox2(true)}
                        className="box1"
                    >
                        <p>Hipertrofia</p>
                        {box2 &&
                            <ArrowDropDownIcon />
                        }
                        {!box2 &&
                            <ArrowDropUpIcon />
                        }
                    </div>
                    <div
                        style={{height: box2 ? '170px' : '0' }}
                        className="box2"
                    >
                        <p>
                            Ajuste da ingestão alimentar para ganho de massa muscular e indicação de suplementação eficiente para alcançar seus objetivos tanto na estética quanto na saúde.
                        </p>
                    </div>
                </div>
                <div className="boxes">
                    <div
                        onClick={() => box3 ? setBox3(false) : setBox3(true)}
                        className="box1"
                    >
                        <p>Distúrbios gastrointestinais</p>
                        {box3 &&
                            <ArrowDropDownIcon />
                        }
                        {!box3 &&
                            <ArrowDropUpIcon />
                        }
                    </div>
                    <div
                        style={{height: box3 ? '170px' : '0' }}
                        className="box2"
                    >
                        <p>
                            Estratégias nutricionais para Supercrescimento bacteriano (SIBO), Supercrescimento fúngico (SIFO), distensão abdominal (alta produção de gases), Síndrome do intestino irritável e mais.
                        </p>
                    </div>
                </div>
                <div className="boxes">
                    <div
                        onClick={() => box4 ? setBox4(false) : setBox4(true)}
                        className="box1"
                    >
                        <p>Doenças autoimunes</p>
                        {box4 &&
                            <ArrowDropDownIcon />
                        }
                        {!box4 &&
                            <ArrowDropUpIcon />
                        }
                    </div>
                    <div
                        style={{height: box4 ? '170px' : '0' }}
                        className="box2"
                    >
                        <p>
                            Dermatite atópica, psoríase artrite reumatoide, tireoidite de hashimoto e mais. No início iremos fazer um checkup para identificar possíveis doenças que ainda não se manifestaram.
                        </p>
                    </div>
                </div>
                <div className="boxes">
                    <div
                        onClick={() => box5 ? setBox5(false) : setBox5(true)}
                        className="box1"
                    >
                        <p>Emagrecimento avançado</p>
                        {box5 &&
                            <ArrowDropDownIcon />
                        }
                        {!box5 &&
                            <ArrowDropUpIcon />
                        }
                    </div>
                    <div
                        style={{height: box5 ? '170px' : '0' }}
                        className="box2"
                    >
                        <p>
                            Estratégias nutricionais para otimizar a perda de peso e fugir do efeito platô! Algumas podem ser utilizadas, como lowcarb, cetogenica, jejum intermitente ou ciclo de carboidratos, por exemplo.
                        </p>
                    </div>
                </div>
                <div className="boxes">
                    <div
                        onClick={() => box6 ? setBox6(false) : setBox6(true)}
                        className="box1"
                    >
                        <p>Sensibilidade alimentar</p>
                        {box6 &&
                            <ArrowDropDownIcon />
                        }
                        {!box6 &&
                            <ArrowDropUpIcon />
                        }
                    </div>
                    <div
                        style={{height: box6 ? '170px' : '0' }}
                        className="box2"
                    >
                        <p>
                            Análise de possíveis sensibilidades e orientação de exclusões e tratamento para alívio dos sintomas, de forma que não fique desconfortável e que seja possível manter uma boa dieta.
                        </p>
                    </div>
                </div>
            </div>
        </Container>
    );
}

export default Skills;